.nav-bar {
    .nav-item {
        &.realm-nav-item {
            cursor: auto;

            &:hover {
                background-color: unset;
            }

            &::before {
                display: none;
            }
        }

        .user-realm-label {
            margin: 0 var(--spacing);
            white-space: nowrap;
            border-radius: var(--br-max);
            border: 1px solid var(--sub-antd);
            padding: var(--spacing-xxs) var(--spacing);
            border-color: var(--sub-antd);
            font-size: var(--fs-md);
            color: var(--sub-antd);
        }
    }
}

.menu-list {
    .menu-item.activated {
        .anticon-inbox {
            color: white;
        }
    }
}