html {
  font-size: 14px;
}

body {
  font-size: 1rem;
  font-family: var(--ff);
  color: var(--color-normal);
  line-height: 1.575;
}

* {
  scroll-behavior: smooth;
  scrollbar-width: thin;
  box-sizing: border-box;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: unset;
  margin: 0;
  padding: 0;
  font-size: unset;
}

label {
  cursor: unset;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 4px #ddd;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #dadada;
  border-radius: 1rem;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #c2c2c2;
}

.primary-theme {
  color: var(--color-primary);
  background-color: var(--bgColor-primary);
  border-color: var(--bdColor-primary);
}
.primary-lighter {
  color: var(--color-primaryLighter);
  background-color: var(--bgColor-primaryLighter);
  border-color: var(--bdColor-primaryLighter);
}
.primary-darker {
  color: var(--color-primaryDarker);
  background-color: var(--bgColor-primaryDarker);
  border-color: var(--bdColor-primaryDarker);
}

