:root {
	--_scales-0: borderRadiusScale;
	--_scales-1: spacingScale;

	--transparent: transparent;

	--black: #000;

	--white: #fff;

	--vendor: #072d5c;

	--spacing-scale-tight: 0.5;
	--spacing-scale-normal: 1;
	--spacing-scale-loose: 1.5;
	--spacing-scale: var(--spacing-scale-normal);

	--border-radius-scale-sharper: 1;
	--border-radius-scale-normal: 2;
	--border-radius-scale-dull: 3;
	--border-radius-scale: var(--border-radius-scale-normal);

	--min-height-navbar: 54px;
	--min-height-header: 52px;
	--min-height-header-large: 60px;
	--min-height-header-small: 44px;
	--min-height-content: 32px;
	--min-height-content-large: 40px;
	--min-height-content-small: 24px;
	--min-height: var(--min-height-header);

	--size-1: 1px;
	--size-2: 2px;
	--size-3: 3px;
	--size-4: 4px;
	--size-5: 5px;
	--size-6: 6px;
	--size-7: 7px;
	--size-8: 8px;
	--size-9: 9px;
	--size-10: 10px;
	--size-none: 0px;
	--size-px: 1px;
	--size-xxs: 2px;
	--size-xs: 5px;
	--size-sm: 10px;
	--size-md: 15px;
	--size-lg: 20px;
	--size-xl: 30px;
	--size-2xl: 40px;
	--size-3xl: 50px;
	--size-4xl: 60px;
	--size-5xl: 70px;
	--size-6xl: 80px;
	--size: var(--size-1);

	--spacing-0: 0;
	--spacing-1: 1px;
	--spacing-2: 2px;
	--spacing-5: 5px;
	--spacing-10: 10px;
	--spacing-15: 15px;
	--spacing-20: 20px;
	--spacing-25: 25px;
	--spacing-30: 30px;
	--spacing-35: 35px;
	--spacing-40: 40px;
	--spacing-45: 45px;
	--spacing-50: 50px;
	--spacing-neg-1: -1px;
	--spacing-neg-2: -2px;
	--spacing-neg-5: -5px;
	--spacing-neg-10: -10px;
	--spacing-neg-15: -15px;
	--spacing-neg-20: -20px;
	--spacing-neg-25: -25px;
	--spacing-neg-30: -30px;
	--spacing-neg-35: -35px;
	--spacing-neg-40: -40px;
	--spacing-neg-45: -45px;
	--spacing-neg-50: -50px;
	--spacing: var(--spacing-1);

	--spacing-none: calc(0 * var(--spacing-scale));
	--spacing-px: calc(1px * var(--spacing-scale));
	--spacing-xxs: calc(2px * var(--spacing-scale));
	--spacing-xs: calc(5px * var(--spacing-scale));
	--spacing-sm: calc(10px * var(--spacing-scale));
	--spacing-md: calc(15px * var(--spacing-scale));
	--spacing-lg: calc(20px * var(--spacing-scale));
	--spacing-xl: calc(30px * var(--spacing-scale));
	--spacing-2xl: calc(40px * var(--spacing-scale));
	--spacing-3xl: calc(50px * var(--spacing-scale));
	--spacing-4xl: calc(60px * var(--spacing-scale));
	--spacing-5xl: calc(70px * var(--spacing-scale));
	--spacing-6xl: calc(80px * var(--spacing-scale));
	--spacing-neg-px: calc(-1px * var(--spacing-scale));
	--spacing-neg-xxs: calc(-2px * var(--spacing-scale));
	--spacing-neg-xs: calc(-5px * var(--spacing-scale));
	--spacing-neg-sm: calc(-10px * var(--spacing-scale));
	--spacing-neg-md: calc(-15px * var(--spacing-scale));
	--spacing-neg-lg: calc(-20px * var(--spacing-scale));
	--spacing-neg-xl: calc(-30px * var(--spacing-scale));
	--spacing-neg-2xl: calc(-40px * var(--spacing-scale));
	--spacing-neg-3xl: calc(-50px * var(--spacing-scale));
	--spacing-neg-4xl: calc(-60px * var(--spacing-scale));
	--spacing-neg-5xl: calc(-70px * var(--spacing-scale));
	--spacing-neg-6xl: calc(-80px * var(--spacing-scale));
	--spacing: var(--spacing-md);

	--ff-base: "Roboto Condensed", Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	--ff-sans-serif: "Roboto Condensed", Roboto, -apple-system, BlinkMacSystemFont, "Segoe UI", "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
	--ff-mono: Menlo, Monaco, "Source Code Pro", "Segoe UI Mono", "Roboto Mono", ui-monospace, "Oxygen Mono", "Ubuntu Monospace", "Fira Mono", "Droid Sans Mono", "Courier New", monospace;
	--ff-serif: "Palatino Linotype", Palatino, Palladio, "URW Palladio L", "Book Antiqua", Baskerville, "Bookman Old Style", "Bitstream Charter", "Nimbus Roman No9 L", Garamond, "Apple Garamond", "ITC Garamond Narrow", "New Century Schoolbook", "Century Schoolbook", "Century Schoolbook L", Georgia, serif;
	--ff: var(--ff-base);

	--fs-0: 0;
	--fs-1: 0.25rem;
	--fs-2: 0.5rem;
	--fs-3: 0.75rem;
	--fs-4: 1rem;
	--fs-5: 1.25rem;
	--fs-6: 1.5rem;
	--fs-8: 2rem;
	--fs-10: 2.5rem;
	--fs-px: 0.3667rem;
	--fs-xxs: 0.55rem;
	--fs-xs: 0.75rem;
	--fs-sm: 0.875rem;
	--fs-md: 1rem;
	--fs-base: 1rem;
	--fs-lg: 1.15rem;
	--fs-xl: 1.3rem;
	--fs-2xl: 1.4rem;
	--fs-3xl: 1.575rem;
	--fs-4xl: 1.7rem;
	--fs-5xl: 2rem;
	--fs-6xl: 3rem;
	--fs-7xl: 4rem;
	--fs-8xl: 5rem;
	--fs: var(--fs-base);

	--lh-xxs: 0.5;
	--lh-xs: 0.6;
	--lh-sm: 0.8;
	--lh-md: 1;
	--lh-lg: 1.25;
	--lh-xl: 1.375;
	--lh-2xl: 1.5;
	--lh-3xl: 1.75;
	--lh-4xl: 2.25;
	--lh: var(--lh-md);

	--ls-xxs: -0.04rem;
	--ls-xs: -0.02rem;
	--ls-sm: -0.01rem;
	--ls-md: normal;
	--ls-lg: 0.04rem;
	--ls-xl: 0.07rem;
	--ls-2xl: 0.1rem;
	--ls-3xl: 0.125rem;
	--ls-4xl: 0.15rem;
	--ls: var(--ls-md);

	--br-0: calc(0 * var(--border-radius-scale));
	--br-none: calc(0 * var(--border-radius-scale));
	--br-sm: calc(0.125rem * var(--border-radius-scale));
	--br-md: calc(0.25rem * var(--border-radius-scale));
	--br-normal: calc(0.25rem * var(--border-radius-scale));
	--br-lg: calc(0.5rem * var(--border-radius-scale));
	--br-xl: calc(1rem * var(--border-radius-scale));
	--br-max: calc(99999px * var(--border-radius-scale));
	--br-full: calc(99999px * var(--border-radius-scale));
	--br: var(--br-md);

	--bdColor-none: transparent;
	--bdColor-contrast: #ffffff;
	--bdColor-faint: #f8f8f8;
	--bdColor-subtle: #f0f0f1;
	--bdColor-normal: #e1e1e3;
	--bdColor-blunt: #d2d2d6;
	--bdColor-extreme: #9696a0;
	--bdColor-separator: #aaaaaa;
	--bdColor: var(--bdColor-normal);

	--bd-none: 0 solid var(--bdColor-none);
	--bd-contrast: var(--size-px) solid var(--bdColor-contrast);
	--bd-faint: var(--size-px) solid var(--bdColor-faint);
	--bd-subtle: var(--size-px) solid var(--bdColor-subtle);
	--bd-normal: var(--size-px) solid var(--bdColor-normal);
	--bd-blunt: var(--size-px) solid var(--bdColor-blunt);
	--bd-extreme: var(--size-px) solid var(--bdColor-extreme);
	--bd-separator: var(--size-3) solid var(--bdColor-separator);
	--bd: var(--bd-normal);

	--bs-0: unset;
	--bs-1: 0 1px 3px rgba(0, 0, 0, 0.12), 0 1px 2px rgba(0, 0, 0, 0.24);
	--bs-2: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
	--bs-3: 0 10px 20px rgba(0, 0, 0, 0.19), 0 6px 6px rgba(0, 0, 0, 0.23);
	--bs-4: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
	--bs-5: 0 19px 38px rgba(0, 0, 0, 0.3), 0 15px 12px rgba(0, 0, 0, 0.22);
	--bs-1-ambient: 0 0 2px 0px rgba(0, 0, 0, 0.6);
	--bs-2-ambient: 0 0 2px 1px rgba(0, 0, 0, 0.55);
	--bs-3-ambient: 0 0 5px 1px rgba(0, 0, 0, 0.5);
	--bs-4-ambient: 0 0 6px 2px rgba(0, 0, 0, 0.45);
	--bs-5-ambient: 0 0 8px 3px rgba(0, 0, 0, 0.4);
	--bs-1-inset: 0 1px 3px rgba(0, 0, 0, 0.12) inset, 0 1px 2px rgba(0, 0, 0, 0.24) inset;
	--bs-2-inset: 0 3px 6px rgba(0, 0, 0, 0.16) inset, 0 3px 6px rgba(0, 0, 0, 0.23) inset;
	--bs-3-inset: 0 10px 20px rgba(0, 0, 0, 0.19) inset, 0 6px 6px rgba(0, 0, 0, 0.23) inset;
	--bs-4-inset: 0 14px 28px rgba(0, 0, 0, 0.25) inset, 0 10px 10px rgba(0, 0, 0, 0.22) inset;
	--bs-5-inset: 0 19px 38px rgba(0, 0, 0, 0.3) inset, 0 15px 12px rgba(0, 0, 0, 0.22) inset;
	--bs: var(--bs-1-ambient);

	--ts-0: unset;
	--ts-1: 0 1px 0 var(--color-gray-400), 0 1px 1px var(--color-gray-600);
	--ts-2: 0 1px 1px var(--color-gray-500), 0 2px 2px var(--color-gray-700);
	--ts-dark: 0px 1px 2px rgb(0, 0, 0, 50%), 0px 1px 2px rgb(0, 0, 0, 20%);
	--ts-light: 0px 1px 2px rgb(255, 255, 255), 0px 1px 2px rgb(0, 0, 0, 70%);
	--ts: var(--ts-1);

	--gradient-hovered: linear-gradient(45deg, #1d21290a, #1d21290a);

