.slider {
  --position: 25%;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  .slider-container {
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
  .slider-button {
    position: absolute;
    color: var(--color-white);
    transform: translate(-50%, -50%);
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    & svg {
      font-size: var(--fs-5xl);
    }
    &.button-prev {
      bottom: var(--position);
      left: var(--position);
    }
    &.button-next {
      bottom: var(--position);
      left: calc(var(--position) + 45px);
    }
  }
  .slider-progress {
    position: absolute;
    top: 50%;
    left: 24%;
    color: var(--color-white);
  }
  .slider-progress-text {
    font-size: var(--fs-6xl);
    display: inline-block;
    transform: translate(-4px, 16px);
    font-weight: bold;
  }
  .slider-amount {
    font-size: var(--fs-4xl);
  }
  .slider-item {
    width: 100%;
    height: 100%;
  }
}
