.truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.no-overflow {
  overflow: hidden;
}

.clamp {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  overflow: hidden;
  &-2 {
    -webkit-line-clamp: 2;
  }
  &-3 {
    -webkit-line-clamp: 3;
  }
  &-5 {
    -webkit-line-clamp: 5;
  }
}

.no-outline {
  outline: none !important;
}

.deep-no-outline {
  @extend .no-outline;
  *:not(input):not(textarea):not(select) {
    @extend .no-outline;
  }
}

.no-transition {
  transition-duration: 0s !important;
  animation-duration: 0s !important;
}

.deep-no-transition {
  @extend .no-transition;
  * {
    @extend .no-transition;
  }
}

.keep-line:before {
  display: inline-block;
  width: 0;
  visibility: hidden;
  content: "\a0";
}
