.article-wrap {
  position: relative;
  width: 100%;
  padding: var(--spacing-lg);
  height: 100%;
  overflow-y: scroll;
  .article-heading {
    width: 80%;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, auto);
    margin: 0 auto var(--spacing-lg);
  }
  .article-heading {
    grid-row: 1 / span 1;
  }
  .article-heading-title {
    font-weight: bold;
    font-size: var(--fs-4xl);
    margin-bottom: var(--spacing-md);
  }
  .article-heading-media {
    border-radius: var(--br-md);
    grid-row: 2 / span 1;
    aspect-ratio: 21/9;
    object-fit: cover;
    width: 100%;
  }
  .article-content {
    max-width: 900px;
    margin: 0 auto;
  }
  .article-heading-introduce {
    width: 80%;
  }
}
