.app-wrapper {
  height: calc(var(--vh, 1vh) * 100);
}

.ant-modal-root {
  .paginated-table__wrapper {
    .compose-header.paginated-header {
      padding: var(--spacing-sm) var(--spacing);
    }
  }
}

.paginated-list__content {
  flex: 1;

  .entity-item-shell {
    border-bottom: var(--bd);
  }
}

.paginated-table__content {
  flex: 1;
}

.menu-col {
  .menu-section-menu {
    .menu-item.selected {
      color: var(--main-major);
    }
  }
}
