.post-paginate-item {
  padding: calc(var(--spacing-sm) - 1px) var(--spacing-md);
  min-height: 120px;
  max-height: 120px;
  height: 120px;
  display: flex;
  align-items: center;
  border-radius: var(--br-sm);
  cursor: pointer;
  .item-media {
    flex-shrink: 0;
    width: 100px;
    height: 100%;
    margin-right: var(--spacing-sm);
  }
  .item-media-img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: var(--br-normal);
  }
  .item-content {
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-direction: column;
  }
  .item-content-title {
    flex-shrink: 0;
    font-weight: bold;
    margin: var(--spacing-xs) 0;
  }
  .item-content-text {
    overflow: hidden;
    height: 100%;
    margin-bottom: 0;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    text-overflow: ellipsis;
  }
}
