:root {
	--main-primary: #4f91ff;
	--main-primaryLighter: #d8e6ff;
	--main-primaryDarker: #2264d1;
	--main-secondary: #76a9ff;
	--main-secondaryLighter: #cee5ff;
	--main-secondaryDarker: #2979ff;
	--main-tertiary: #e1e1e3;
	--main-tertiaryLighter: #f0f0f1;
	--main-tertiaryDarker: #b4b4bb;
	--main-antd: #1890FF;
	--main-major: #2979ff;
	--main-minor: #f7fafc;
	--main-info: #13c2c2;
	--main-success: #43a047;
	--main-warning: #ffb41f;
	--main-danger: #ff4d4f;
	--main-salmon: #fccbc8;
	--main-grey: #4a4b57;
	--main-purple: #93258E;
	--main-orange: #EF6C1F;
	--main-indigo: #4B0082;

	--sub-primary: #609cff;
	--sub-primaryLighter: #dbe8ff;
	--sub-primaryDarker: #3873d5;
	--sub-secondary: #83b1ff;
	--sub-secondaryLighter: #d2e7ff;
	--sub-secondaryDarker: #3e86ff;
	--sub-tertiary: #e4e4e5;
	--sub-tertiaryLighter: #f1f1f2;
	--sub-tertiaryDarker: #bbbbc1;
	--sub-antd: #40A9FF;
	--sub-major: #4f91ff;
	--sub-minor: #edf2f7;
	--sub-info: #36cfc9;
	--sub-success: #65b168;
	--sub-warning: #ffc147;
	--sub-danger: #ff7875;
	--sub-salmon: #fff0f0;
	--sub-grey: #5b5c68;
	--sub-purple: #a345aE;
	--sub-orange: #fF8C3F;
	--sub-indigo: #5a009c;

	--color-primary: var(--color-contrast);
	--color-primaryLighter: var(--color-normal);
	--color-primaryDarker: var(--color-contrast);
	--color-secondary: var(--color-contrast);
	--color-secondaryLighter: var(--color-normal);
	--color-secondaryDarker: var(--color-contrast);
	--color-tertiary: var(--color-normal);
	--color-tertiaryLighter: var(--color-normal);
	--color-tertiaryDarker: var(--color-contrast);
	--color-antd: var(--color-contrast);
	--color-major: var(--color-contrast);
	--color-minor: var(--color-normal);
	--color-info: var(--color-contrast);
	--color-success: var(--color-contrast);
	--color-warning: var(--color-normal);
	--color-danger: var(--color-contrast);
	--color-hovered: var(--color-normal);
	--color-activated: var(--color);
	--color-highlighted: var(--color);
	--color-disabled: var(--color-subtle);
	--color-salmon: var(--color);
	--color-grey: var(--color-contrast);
	--color-purple: var(--color-contrast);
	--color-orange: var(--color-contrast);
	--color-indigo: var(--color-contrast);

	--bgColor-primary: var(--main-primary);
	--bgColor-primaryLighter: var(--main-primaryLighter);
	--bgColor-primaryDarker: var(--main-primaryDarker);
	--bgColor-secondary: var(--main-secondary);
	--bgColor-secondaryLighter: var(--main-secondaryLighter);
	--bgColor-secondaryDarker: var(--main-secondaryDarker);
	--bgColor-tertiary: var(--main-tertiary);
	--bgColor-tertiaryLighter: var(--main-tertiaryLighter);
	--bgColor-tertiaryDarker: var(--main-tertiaryDarker);
	--bgColor-major: var(--main-major);
	--bgColor-minor: var(--main-minor);
	--bgColor-info: var(--main-info);
	--bgColor-success: var(--main-success);
	--bgColor-warning: var(--main-warning);
	--bgColor-danger: var(--main-danger);
	--bgColor-hovered: var(--bgColor-tertiaryLighter);
	--bgColor-activated: var(--bgColor-secondaryLighter);
	--bgColor-highlighted: var(--color-blue-200);
	--bgColor-visited: var(--color-gray-100);
	--bgColor-disabled: var(--color-gray-100);

	--bdColor-primary: var(--sub-primary);
	--bdColor-primaryLighter: var(--sub-primaryLighter);
	--bdColor-primaryDarker: var(--sub-primaryDarker);
	--bdColor-secondary: var(--sub-secondary);
	--bdColor-secondaryLighter: var(--sub-secondaryLighter);
	--bdColor-secondaryDarker: var(--sub-secondaryDarker);
	--bdColor-tertiary: var(--sub-tertiary);
	--bdColor-tertiaryLighter: var(--sub-tertiaryLighter);
	--bdColor-tertiaryDarker: var(--sub-tertiaryDarker);
	--bdColor-major: var(--sub-major);
	--bdColor-minor: var(--sub-minor);
	--bdColor-info: var(--sub-info);
	--bdColor-success: var(--sub-success);
	--bdColor-warning: var(--sub-warning);
	--bdColor-danger: var(--sub-danger);
	--bdColor-hovered: var(--bdColor-tertiaryLighter);
	--bdColor-activated: var(--bdColor-secondaryLighter);
	--bdColor-highlighted: var(--color-blue-200);
	--bdColor-visited: var(--color-gray-100);
	--bdColor-disabled: var(--color-neutral-200);

	--spacing-scale: 1;

	--border-radius-scale: 2;
}